.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(45deg, rgb(156,14,156), midnightblue);
    &-liste {
        height: 100%;
        list-style-type: none;
        background: linear-gradient(45deg, rgb(156,14,156), midnightblue);
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    &-item {
        margin: 0 15px;
        cursor: pointer;
        a {
            text-transform: uppercase;
            color: #f1f1f1;
            text-decoration: none;
        }
    }
    &-toggle {
        display: none;
        position: absolute;
        top:10px;
        right:7px;
        padding: 5px;
        color: #000;
        font-size: 18px;
    }
    
    @media screen and (max-width:500px) {
        &-liste {
            flex-direction: column;
            height: auto;
        }
        &-item {
            height: 70px;
            width: 100%;
            border-bottom: 1px solid rgba(255, 255, 255, 0.6);
            text-align: center;
            line-height: 70px;
            &:nth-child(1) {
                border-top: 1px solid rgba(255, 255, 255, 0.6);
                margin-top: 50px
            }
        }
        &-toggle {
            display: block;
        }
    }
}