.titre-form {
    text-align: center;
    font-size: 55px;
}
.container-form {
    padding: 10px;
    max-width: 900px;
    margin: 30px 0 0;
    display: flex;
    flex-direction: column;
    label {
        font-size: 20px
    }
    input, textarea {
        padding: 10px;
        margin-bottom: 25px;
        font-size: 18px;
    }
    textarea {
        min-height: 200px;
        font-family: Arial, Helvetica, sans-serif;
    }
    button {
        padding: 10px 15px;
        width: 200px;
        background: transparent;
        border: 1px solid rgba(0, 0, 0, 0.8)
    }
}